@import '../../../styles/abstracts/mixins';
@import '../../../styles/base/variables';

html,
body {
	width: 100%;
	height: 100%;
	overflow: hidden;
}

body {
	background: $neutralShade7 url("data:image/png;base64,") 0;

	display: flex;
	justify-content: center;
	align-items: center;
}

.content-error {
	text-align: center;

	h1 {
		text-shadow: 0 3px 0px $accent3Shade3, 0 6px 0px $neutralShade7;
		color: $accent3Color;
		font-size: 6em;
		font-weight: 700;
		line-height: 0.6em;
	}

	p {
		color: #ccc;
		font-family: monospace;
		font-size: 0.95em;
		font-weight: 300;
		line-height: 7em;
	}
}

.sheep {
	display: inline-block;
	position: relative;
	font-size: 1em;
	$skin: #211e21;

	* {
		transition: transform 0.3s;
	}

	.top {
		position: relative;
		top: 0;
		animation: bob 1s infinite;
	}

	&:hover {
		.head {
			transform: rotate(0deg);

			.eye {
				width: 1.25em;
				height: 1.25em;

				&:before {
					right: 30%;
				}
			}
		}

		.top {
			animation-play-state: paused
		}
	}

	.head {
		display: inline-block;
		width: 5em;
		height: 5em;
		border-radius: 100%;
		background: $skin;
		vertical-align: middle;
		position: relative;
		top: 1em;
		transform: rotate(30deg);

		&:before {
			content: '';
			display: inline-block;
			width: 80%;
			height: 50%;
			background: $skin;
			position: absolute;
			bottom: 0;
			right: -10%;
			border-radius: 50% 40%;
		}

		&:hover {
			.ear {

				&.one,
				&.two {
					transform: rotate(0deg);
				}
			}
		}

		.eye {
			display: inline-block;
			width: 1em;
			height: 1em;
			border-radius: 100%;
			background: white;
			position: absolute;
			overflow: hidden;

			&:before {
				content: '';
				display: inline-block;
				background: black;
				width: 50%;
				height: 50%;
				border-radius: 100%;
				position: absolute;
				right: 10%;
				bottom: 10%;
				transition: all 0.3s;
			}

			&.one {
				right: -2%;
				top: 1.7em;
			}

			&.two {
				right: 2.5em;
				top: 1.7em;
			}
		}

		.ear {
			background: $skin;
			width: 50%;
			height: 30%;
			border-radius: 100%;
			position: absolute;

			&.one {
				left: -10%;
				top: 5%;
				transform: rotate(-30deg);
			}

			&.two {
				top: 2%;
				right: -5%;
				transform: rotate(20deg);
			}
		}
	}

	.body {
		display: inline-block;
		width: 7em;
		height: 7em;
		border-radius: 100%;
		background: white;
		position: relative;
		vertical-align: middle;
		margin-right: -3em;
	}

	.legs {
		display: inline-block;
		position: absolute;
		top: 80%;
		left: 10%;
		z-index: -1;

		.leg {
			display: inline-block;
			background: darken($skin, 5%);
			width: 0.5em;
			height: 2.5em;
			margin: 0.2em;
		}
	}

	&:before {
		content: '';
		display: inline-block;
		position: absolute;
		top: 112%;
		width: 100%;
		height: 10%;
		border-radius: 100%;
		background: rgba(black, 0.4);
	}
}

@keyframes bob {
	0% {
		top: 0;
	}

	50% {
		top: 0.2em;
	}
}
